import luPopupWrapper from "@/components/lu-popup-wrapper/lu-popup-wrapper.vue";
import picture from "../../common/picture.js";
import uniFav from '@/components/uni-fav/uni-fav.vue'
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniSection from '@/components/uni-section/uni-section.vue'
import uniRate from '@/components/uni-rate/uni-rate.vue'
import evaluateData from '../../common/list.js';
import clCardDel from "@/components/cl-cardDel/cl-cardDel";
import uniEvaluate from '../../components/xiujun-evaluate/uni-evaluate.vue';
export default {
	components: {
		uniFav,
		uniNavBar,
		uniSection,
		uniRate,
		uniEvaluate,
		luPopupWrapper,
	},
	mixins:[clCardDel],
	data() {
		return {
			showUp: true,
			cardCur: 0,
			dotStyle: false,
			towerStart: 0,
			direction: '',
			swiperList: [],
			fold:true,
			evaluateData:evaluateData,
			rateData:8.5,
			//弹出层
			type:"bottom",// left right top bottom center
			transition:"slider",//none slider fade
			backgroundColor:'#FFF',
			active:false,
			height:"100%",
			width:"100%",
			popupId:1,
			maskShow:true,
			maskClick:true,
		};
	},
	onLoad() {
		let that = this;
		that.swiperList = picture.swiperList;
		this.TowerSwiper('swiperList');
		// 初始化towerSwiper 传已有的数组名即可
	},
	methods: {
		DotStyle(e) {
			this.dotStyle = e.detail.value
		},
		// cardSwiper
		cardSwiper(e) {
			this.cardCur = e.detail.current
		},
		// towerSwiper
		// 初始化towerSwiper
		TowerSwiper(name) {
			let list = this[name];
			for (let i = 0; i < list.length; i++) {
				list[i].zIndex = parseInt(list.length / 2) + 1 - Math.abs(i - parseInt(list.length / 2))
				list[i].mLeft = i - parseInt(list.length / 2)
			}
			this.swiperList = list
		},

		// towerSwiper触摸开始
		TowerStart(e) {
			this.towerStart = e.touches[0].pageX
		},

		// towerSwiper计算方向
		TowerMove(e) {
			this.direction = e.touches[0].pageX - this.towerStart > 0 ? 'right' : 'left'
		},

		// towerSwiper计算滚动
		TowerEnd(e) {
			let direction = this.direction;
			let list = this.swiperList;
			if (direction == 'right') {
				let mLeft = list[0].mLeft;
				let zIndex = list[0].zIndex;
				for (let i = 1; i < this.swiperList.length; i++) {
					this.swiperList[i - 1].mLeft = this.swiperList[i].mLeft
					this.swiperList[i - 1].zIndex = this.swiperList[i].zIndex
				}
				this.swiperList[list.length - 1].mLeft = mLeft;
				this.swiperList[list.length - 1].zIndex = zIndex;
			} else {
				let mLeft = list[list.length - 1].mLeft;
				let zIndex = list[list.length - 1].zIndex;
				for (let i = this.swiperList.length - 1; i > 0; i--) {
					this.swiperList[i].mLeft = this.swiperList[i - 1].mLeft
					this.swiperList[i].zIndex = this.swiperList[i - 1].zIndex
				}
				this.swiperList[0].mLeft = mLeft;
				this.swiperList[0].zIndex = zIndex;
			}
			this.direction = ""
			this.swiperList = this.swiperList
		},
		changeImg: function() {
			this.showUp = !this.showUp
		},
		navToDetail(){
			uni.navigateTo({
				url:'/pages/detail/more'
				})
		},
		//弹出
		bottom: function() {
			this.width ="100%";
			this.height ="80%";
			this.transition = "slider";
			this.type = "bottom";
			this.show();
		},
		fade: function() {
			this.width ="100%";
			this.height ="80%";
			this.transition = "fade";
			this.type = "bottom";
			this.show();
		},
		slider: function() {
			this.width ="100%";
			this.height ="80%";
			this.transition = "slider";
			this.type = "bottom";
			this.show();
		},
		show: function() {
			this.$refs.luPopupWrapper.show();
		},
		close: function() {
			this.$refs.luPopupWrapper.close();
		},
		closeCallback:function() {
			console.log("关闭后回调");
		},
		
		//卡片
		//设置初始参数
		init(){
			this.number = 2 //card 2
			//设置第2张卡片transform opacity
			this.rotate = 5 //旋转deg 
			this.scale = { x:0.95,y:0.95 } //x 缩小0.9
		},
		//设置获取数据
		getData(){
			let promise = new Promise((resolve,reject)=>{
				let dataGroup = []
				for (var i = 1; i < 6; i++) {
					dataGroup.push({
						src:`../../static/${i}.jpg`,
						sex:Math.round(Math.random()),
						address:'杭州(100km)',
						name:'可爱的小姐姐',
						constellation:'双鱼座',
						number:10,
						old:18
					})
				}
				this.dataList = [...this.dataList,...dataGroup]
				resolve()
			}) 
			return promise
		},
		tapCard(item){
			console.log(item,"点击")
		},
		tapDelCard(){
			this._del()
		},
		navToeva(){
			uni.navigateTo({
				url:"/pages/evaluate/evaluate"
			})
		}
	}
}
